//- INCLUDE STYLE CONPONENTS
@import 'components/reset';
@import 'components/_template';

.container {
    @media (min-width: 1200px) {
        max-width: 1680px;
        padding: 0 45px;
    }
}

.header {
  &__container {
    height: 90px;
    @media #{$sm} {
      height: 75px;
    }
  }
  &__links {
      width: 340px;
      margin-left: 30px;
      display: none;
      &--item {
          text-decoration: none;
          border-bottom: 1px solid rgba(0, 0, 0, 0.3);
          transition: border-bottom-color .3s;
          font-size: 18px;
          line-height: 18px;
          color: #000000;
          display: inline-block;
          height: 18px;
          &:hover,
          &:active,
          &:visited,
          &:focus {
              text-decoration: none;
              color: #000000;
              border-bottom-color: rgba(0,0,0,0)
          }
      }
  }
}

.logo {
    height: 60px;
    width: 136px;
}

.menu-open {
  overflow: hidden;
}

.nav {
  &__link {
    color: $black;
    &:hover,
    &:active,
    &:focus {
      text-decoration: none;
      color: $crimson;
      border-bottom-color: rgba(0,0,0,0);
    }
    @media #{$xl} {
      margin: 0;
      color: white;
    }
  }

  &__item {
    margin: 0 15px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    text-align: center;

    &--active {
      a {
        text-decoration: none;
        color: $crimson;
        border-bottom-color: rgba(0,0,0,0);
        border-bottom: 5px solid $crimson;
      }

      .nav__link {
        color: $crimson;
      }
    }
  }
}

.nav-mobile {
  z-index: 999;
  transition: max-height .3s ease-out;
  @media #{$xl} {
    position: fixed;
    top: 90px;
    overflow: hidden;
    padding: 0;
    left: 0;
    right: 0;
    height: calc(100vh - 90px);
    background-color: #303030;
    overflow-y: auto;
    min-height: 0;
    max-height: 0;
  }
  @media #{$sm} {
    top: 75px;
    height: calc(100vh - 75px);
  }
  &.show {
    max-height: 1000px;
  }
}

.burger-menu {
  width: 40px;
  height: 30px;
  display: block;
  transition: .3s;
  &__elem {
    width: 40px;
    height: 4px;
    background-color: $black;
    display: block;
    margin-bottom: 9px;
    transition: all .2s;
  }
  &.show {
    width: 40px;
    height: 30px;
    position: relative;
    .burger-menu__elem:nth-child(1) {
      background-color: $black;
      transform: translateY(10px) rotate(-45deg);
      height: 3px;
      width: 30px;
    }
    .burger-menu__elem:nth-child(2) {
      background-color: rgba(0,0,0,0);
      transition: all .1s;
    }
    .burger-menu__elem:nth-child(3) {
      background-color: $black;
      transform: translateY(-15px) rotate(45deg);
      height: 3px;
      width: 30px;
    }
  }
}

.main {
  height: 600px;
  background: url(../img/main-bg.jpg) 50% 0 no-repeat;
  background-size: cover;
  @media #{$sm} {
    height: 215px;
  }
  @media #{$xl} {
    height: 390px;
  }
  &__container {
    position: relative;
  }
  &__logo-wrapper {
    padding: 15px;
    padding-bottom: 5px;
    background-color: #fff;
    position: absolute;
    bottom: 30px;

    @media #{$sm} {
      padding: 10px;
      padding-bottom: 5px;
      bottom: 15px;
    }
  }
  &__logo {
    @media #{$sm} {
      width: 190px;
      height: 60px;
    }
  }
}

.ico {
  float: left;
  margin-right: 25px;
  @media #{$md} {
    display: block;
    margin: auto;
    margin-left: 0px;
    margin-top: 0;
    margin-bottom: 10px;
  }
}

.info {
  &__container {
    margin-top: 45px;
    margin-bottom: 45px;
    @media #{$sm} {
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }
  &__list {
    margin-bottom: 50px;
    @media #{$sm} {
      margin-bottom: 30px;
    }
  }
  &__pre-title {
    font-size: 28px;
    margin-bottom: 10px;
    @media #{$sm} {
      font-size: 22px;
      margin-bottom: 0;
    }
  }
  .title-wrapper {
    margin-bottom: 27px;
    @media #{$sm} {
      margin-bottom: 10px;
    }
  }
  &__title {
    margin-bottom: 0;
  }
  &__sell {
    width: 178px;
    height: 36px;
    background: #CB2323;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    color: white;
    margin-left: 16px;
    margin-top: 10px;
    padding-left: 7px;
    position: relative;
    text-decoration: none;
    &:hover::after {
      background-color: rgba(255,255,255, 0);
    }
    &:hover,
    &:active,
    &:visited,
    &:focus {
      text-decoration: none;
      color: white;
    }
    &::after {
      content: '';
      display: block;
      position: absolute;
      width: 134px;
      height: 1px;
      background-color: rgba(255,255,255, .33);
      bottom: 6px;
      transition: background-color .3s;
      @media #{$md} {
        width: 104px;
        bottom: 3px;
      }
    }
    @media #{$md} {
      margin-left: 0;
      width: 138px;
      height: 26px;
      font-size: 14px;
      margin-top: 7px;
    }
  }
  &__sell-ico {
    margin-left: 12px;
    @media #{$md} {
      margin-left: 5px;
    }
  }
  &__item {
    margin-top: 50px;
  }
  &__nums {
    font-weight: 300;
    font-size: 40px;
    line-height: 30px;
    margin-bottom: 10px;
    @media #{$sm} {
      font-size: 24px;
      margin-bottom: 5px;
    }
    @media #{$md} {
      font-size: 34px;
    }
    sup {
      font-size: 26px;
    }
  }
  &__desc {
    @media #{$sm} {

    }
  }
  &__adress {
    margin-bottom: 12px;
    @media #{$lg} {
      margin-bottom: 16px;
    }
    @media #{$md} {
      margin-bottom: 10px;
    }
  }
}

.look-map {
  position: relative;
  cursor: pointer;
  &::after {
    content: '';
    display: block;
    width: 190px;
    height: 1px;
    position: absolute;
    background-color: rgba(166, 23, 68, 0.4);
    bottom: 4px;
    left: 27px;
    transition: background-color .3s;
  }
  &:hover::after {
    background-color: rgba(166, 23, 68, 0);
  }
}

.ico-map {
  margin-right: 4px;
}

.project {
  &__content {
    background-color: $gray;
  }
  &__switch {
    margin-top: 90px;
    margin-bottom: 27px;
    @media #{$md} {
      margin-bottom: 30px;
    }
  }
  &__switch-item {
    font-weight: 600;
    font-size: 30px;
    line-height: 34px;
    text-decoration: none;
    color: $black;
    margin-right: 70px;
    @media #{$md} {
      font-size: 20px;
      line-height: 24px;
      margin-right: 30px;
    }
    &:last-child {
      margin-right: 0;
    }
    &:hover{
      text-decoration: none;
      color: $crimson;
    }
    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 7px;
      background-color: rgba(0,0,0,0)
    }
  }
  &__switch-item.active {
    color: $crimson;
    &::after {
      background-color: $crimson;
    }
  }
  &__about {
    padding-top: 50px;
    padding-bottom: 50px;
    @media #{$md} {
      padding-top: 25px;
    }
  }
  .project__parametrs {
    border-collapse: collapse;
    color: white;
    width: 100%;
    tr {
      border-bottom: 1px solid rgba(196, 196, 196, 0.5);
      @media #{$md} {
        display: flex;
        flex-direction: column;
      }
    }
    th {
      padding-bottom: 20px;
      font-family: Montserrat;
      font-weight: 600;
      font-size: 17px;
      line-height: 19px;
    }
    .first-line {
      @media #{$md} {
        display: none;
      }
    }
    td {
      padding: 13px 0;
    }
    .name-col {
      font-weight: 600;
      font-size: 22px;
      line-height: 29px;
    }
    .square, .price {
      display: none;
      font-family: 'Roboto', sans-serif;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      padding: 0;
      @media #{$md} {
        display: block;
      }
    }
    .square__num, .price__num {
      @media #{$md} {
        padding-top: 0;
      }
    }
    .square__num sup {
      font-size: 14px;
    }
  }
  &__picture {
    background: url('../img/project-bg.jpg') left top no-repeat;
    background-size: cover;
    height: 258px;
    @media (min-width: 768px) {
      height: 623px;
    }
    @media (min-width: 992px) {
      height: auto;
    }
  }
}

.player {
  &__wrapper {
    position: relative;
    display: inline-block;
    @media #{$lg} {
      display: block;
    }
  }
  &__img {
    @media #{$lg} {
      width: 100%;
    }
  }
  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.location {
  background: url('../img/location-bg.svg') repeat-y 100% 45%, $crimson-background;
  padding: 60px 0;
  &__picture-content {
    position: relative;
    @media (min-width: 768px) {
      width: 590px;
      max-width: 80%;
    }
    @media #{$md} {
      max-width: 590px;
    }
  }
  &__about {
    @media (min-width: 992px) {
      padding-left: 0;
    }
  }
  &__video {
    @media #{$lg} {
      margin-top: 25px;
    }
  }
}

.block-map {
  background: url('../img/block-map-bg.svg') no-repeat 5% 100%, $gray;
  height: 650px;
  padding-top: 80px;
  @media #{$xl} {
    height: auto;
    padding-bottom: 45px;
  }
  &__buid,
  &__office {
    position: relative;
    padding-left: 65px;
    img {
      position: absolute;
      left: 0px;
    }
  }
  &__buid {
    margin-bottom: 80px;
    @media #{$xl} {
      margin-bottom: 30px;
    }
  }
  &__places {

    &-icon {
      position: absolute;
      left: 0;
      top: 0;
    }
  }
  &__phone {
    font-weight: 500;
    font-size: 26px;
    line-height: 29px;
    color: #FFFFFF;
    margin-bottom: 10px;
  }
  &__desc {
    font-size: 20px;
    line-height: 30px;
    color: #FFFFFF;
  }
  &__map {
    height: 640px;
    @media #{$xl} {
      margin-top: 30px;
    }
  }
}

.near {
  background: url('../img/near-bg.svg') no-repeat 50% 98%, $gray-background;
  padding: 150px 0 130px;
  @media #{$lg} {
    padding: 30px;
  }
  &__picture-items {
    max-width: 100%;
    width: 550px;
  }
  &__img {
    width: 238px;
    height: 110px;
    background: #FFFFFF;
    position: relative;
    margin: 15px;
    img {
      display: block;
      position: relative;
      max-width: 80%;
      max-height: 80%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.recreation {
  background: $crimson-background;
  .title,
  .text {
    max-width: 540px;
    @media #{$xl} {
      max-width: none;
    }
  }
  &__img {
    background: url('../img/recreation-img.jpg') no-repeat;
    background-size: cover;
    @media #{$lg} {
      height: 300px;
    }
  }
  &__about {
    min-height: 350px;
    padding: 125px 0 200px 150px;
    @media #{$xl} {
      padding: 30px;
      min-height: auto;
    }

  }
}

.slider {
  background: $gray-background;
  padding: 60px 0;
  &__container {
    width: 1320px;
  }
  &__dots-container {
    margin-top: -50px;
  }
  &__dot {
    .slick-slide {
      margin: 0 5px;
      cursor: pointer;
    }
    .slick-current {
      position: relative;
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        box-shadow: inset 0px 0px 0px 10px rgba(166,23,68,1);
      }
    }
  }
}

.sign {
  background: url('../img/sign-bg.png') repeat-x bottom center, $gray;
  padding: 60px 0 100px;

  &__title {
    margin-bottom: 40px;
  }

  .form {
    label {
      width: 100%;
    }
    textarea {
      height: 231px;
    }
    &__help {
      float: right;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      line-height: 20px;
    }
    &__send {
      margin-top: 24px;
    }
    &__privacy {
      width: 536px;
      font-size: 18px;
      line-height: 21px;
      margin-left: 36px;
      @media #{$md} {
        margin-top: 15px;
        width: auto;
      }
    }
    &__privacy-link {
      text-decoration: underline;
      &:hover {
        color: white;
        text-decoration: none;
      }
    }
  }
}

.footer {
  background: #1B1B1B;
  padding: 19px 0;
  .container {
    @media #{$xl} {
      max-width: 100%;
    }
  }
  @media #{$md} {
    padding: 19px 0px;
  }
  &__create {
    color: #fff;
    text-decoration: none;
    &:hover {
      color: #fff;
      text-decoration: none;
    }
  }
}

.balloon {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1060;
  display: block;
  text-align: left;
  background-color: #fff;
  padding: 10px 10px 10px 20px;
  .close {
    position: absolute;
    right: 12px;
    top: 6px;
  }
  &-inner {
    font-size: 18px;
    line-height: 26px;
    color: #000000;
  }
  &__title {
    font-size: 18px;
    font-weight: 600;
    color: #000000;
    padding: 0;
    margin: 0;
  }
  &__content {
    line-height: 1.3;
  }
}


.nav-tabs {
  border: none;
  .nav-link{
    &.active,
    &:hover,
    &:active,
    &:focus {
      border: none;
      border-color: none;
      color: #A61744;
    }
  }
}






