//- Include fonts
@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600|Roboto:400,500&display=swap&subset=cyrillic');

@import 'components/_media';

$black: #101010;
$crimson: #A61744;
$gray: #303030;
$light-gray: #CFCFCF;

$crimson-background: radial-gradient(836.10px at 86.25% 18.44%, rgba(203, 35, 35, 0.74) 0%, rgba(170, 65, 65, 0) 100%), #A61744;
$gray-background: radial-gradient(761.45px at 60.86% 16.45%, rgba(255, 255, 255, 0.83) 0%, rgba(255, 255, 255, 0.14) 100%), #CFCFCF;

body {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 26px;
    color: #101010;
}

img {
    max-width: 100%;
    height: auto;
}

.signature {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    color: #BFBFBF;
    margin-top: 10px;
}

.title {
    font-size: 44px;
    line-height: 48px;
    font-weight: 600;
    margin-bottom: 27px;

    &--gold {
        color: #D0AE85;
    }

    &--white {
        color: white;
    }

    @media #{$md} {
        font-size: 30px;
        line-height: 34px;
    }
}

.text {
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 25px;

    &--white {
        color: #FFFFFF;
    }

    &--dark {
        color: #101010;
    }

    @media #{$md} {
        font-size: 18px;
        line-height: 26px;
    }
}

h3 {
    font-weight: 600;
    font-size: 30px;
    line-height: 34px;
    color: #FFFFFF;
    margin-bottom: 15px;
}

.button {
    cursor: pointer;
    display: inline-block;
    position: relative;
    background: linear-gradient(270.5deg, rgba(203, 35, 35, 0.74) -0.24%, rgba(170, 65, 65, 0) 98.72%), #A61744;
    &:hover {
        background: linear-gradient(270.5deg, rgba(244, 92, 27, 0.74) -0.24%, rgba(170, 65, 65, 0) 98.72%), #BF202D;
    }
    &:active {
        background: linear-gradient(270.5deg, rgba(244, 92, 27, 0.74) -0.24%, rgba(170, 65, 65, 0) 98.72%), #BF202D;
    }
    &.disabled {
        background: linear-gradient(269.88deg, rgba(255, 255, 255, 0.46) -4.33%, rgba(255, 255, 255, 0) 101.23%), #C6C6C6;
    }

    &--standart {
        height: 50px;
        line-height: 50px;
        font-weight: bold;
        font-size: 16px;
        color: white;
        padding: 0 37px;
    }

    &--large {
        height: 73px;
        line-height: 73px;
        font-weight: bold;
        font-size: 22px;
        color: white;
        padding: 0 43px;
        @media #{$sm} {
            width: 100%;
            height: 50px;
            line-height: 50px;
            font-size: 16px;
            padding: 0;
        }
    }
}

.form {
    .form-group {
        margin-bottom: 30px;
    }
    .form-control {
        height: 57px;
        line-height: 44px;
        background: #FFFFFF;
        border: 1px solid #BFBFBF;
        box-sizing: border-box;
        border-radius: 0;
        padding: 0 10px;
        font-size: 18px;
        color: #808080;
        box-shadow: none;
        &:hover,
        &:active,
        &:focus {
            border: 1px solid #A61744;
        }
    }
}